jQuery(document).ready(function ($) {

    /* ==========================================================================
    == TOGGLE MENU MOBILE ==
    ========================================================================== */

    $('.js-toggle-nav').click(function(){
        if ($(this).hasClass("icon-menu")) {
            $(this).removeClass("icon-menu").addClass("icon-close");
            $('.nav__inner').addClass('nav__inner_open');
        } else {
            $(this).removeClass("icon-close").addClass("icon-menu");
            $('.nav__inner').removeClass('nav__inner_open');
        }
        return false;
    });


    /* ==========================================================================
    == PARTENAIRES ==
    ========================================================================== */

    $('.js-part-carousel').slick({
        autoplay: true,
        arrows: false,
        autoplaySpeed: 0,
        slidesToShow: 6,
        slidesToScroll: 1,
        cssEase: 'linear',
        speed: 5000,
        draggable: false,
        lazyLoad: 'progressive',
        pauseOnFocus: false,
        pauseOnHover: false,
        swipe: false,
        touchMove: false,
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 6
                }
            },
            {
                breakpoint: 769,
                settings: {
                    slidesToShow: 4
                }
            },
            {
                breakpoint: 520,
                settings: {
                    slidesToShow: 2
                }
            }
        ]
    });



    /* ==========================================================================
    == REALISATIONS ==
    ========================================================================== */

    $('.rea__filter').click(function(){
        $(".rea__item").addClass('is-hidden');
        $(".rea__item[data-filter='"+$(this).attr('data-filter')+"']").removeClass("is-hidden");
        $('.rea__filter').removeClass('is-active');
        $(this).addClass('is-active');
        return false;
    });

    $('.rea__filter[data-filter="*"]').click(function(event) {
        $(".rea__item").removeClass('is-hidden');
        return false;
    });

    $('.js-galerie-first').click(function(e) {
        e.preventDefault();
        $(this).closest(".rea__item").find(".js-galerie-item:first").click();
    });

    $('.rea__item').each(function() {
        $(this).magnificPopup({
            delegate: '.js-galerie-item',
            type: 'image',
            gallery: {
                enabled: true,
                tCounter: ''
            }
        });
    });
    var n = $(".msnry__container").imagesLoaded(function () {
        n.masonry({ itemSelector: ".msnry__item" });
    });
    $(".msnry__container").each(function () {
        $(this).magnificPopup({ delegate: ".msnry__item", type: "image", gallery: { enabled: !0, tCounter: "" } });
    }),
        $(".cookiebanner__btn").click(function () {
            return $(".cookiebanner").removeClass("cookiebanner_active"), !1;
        }),
        1 !=
            (function (e) {
                for (var n = e + "=", o = document.cookie.split(";"), s = 0; s < o.length; s++) {
                    for (var i = o[s]; " " == i.charAt(0); ) i = i.substring(1, i.length);
                    if (0 == i.indexOf(n)) return i.substring(n.length, i.length);
                }
                return null;
            })("cookies") && ($(".cookiebanner").addClass("cookiebanner_active"), (document.cookie = "cookies=1; expires=Thu, 2 Aug 2030 20:47:11 UTC; path=/"));

});